import WithCouhnter from "./WithCounter"

 function ClickCounter ({count,increment , title ,color}){



    return(<>
    <h1>{title}</h1>
    <div className="container text-center mt-4">
            <button className="btn btn-primary m-2">{count}</button>
            <button 
                type="button" 
                className= {`btn btn-${color} m-2`}
                onClick={increment}
            >
                click to Increment
            </button>
        </div>
    
    </>)
}
export default WithCouhnter(ClickCounter)