
import { useState } from "react"

export default function WithCouhnter(OriginComponent){
    const Newcomponenet =(props)=>{
        const [count , setcount]=useState(0)
        const defaultProps = {
            count,
            title: '',
            color: 'danger',
         
          };

          const newprops ={...defaultProps , ...props}
        const increment=(e)=>{
            e.preventDefault()
            setcount(prevent => prevent + 1)
        }

        return(
            <>
               <OriginComponent increment={increment}  {...newprops} />
            </>
        )
    }
    return Newcomponenet
   

}